"use client";
import React, { useState, useEffect } from "react";
import { Star } from "lucide-react";

const SIX_HOURS = 6 * 60 * 60 * 1000;
const THIRTY_MINUTES = 30 * 60 * 1000;

const initialTasks = [
  {
    id: 1,
    title: "Collect 10 plastic bottles",
    description: "Find and collect 10 bottles from your area.",
    difficulty: "Easy",
    points: 10,
    images: [],
    startedAt: null,
    lastCompleted: null,
  },
  {
    id: 2,
    title: "Clean a park bench",
    description: "Wipe down and clean a park bench thoroughly.",
    difficulty: "Medium",
    points: 25,
    images: [],
    startedAt: null,
    lastCompleted: null,
  },
  {
    id: 3,
    title: "Pick up 15 pieces of trash",
    description: "Collect 15 random pieces of trash.",
    difficulty: "Hard",
    points: 50,
    images: [],
    startedAt: null,
    lastCompleted: null,
  },
];

export default function TasksPage() {
  const [tasks, setTasks] = useState([]);
  const [activeTaskId, setActiveTaskId] = useState(null);
  const [userPoints, setUserPoints] = useState(0);
  const [flyingPoints, setFlyingPoints] = useState([]);

  useEffect(() => {
    const savedTasks = JSON.parse(localStorage.getItem("tasks_v6"));
    const savedActive = JSON.parse(localStorage.getItem("active_task_v6"));
    const savedPoints = JSON.parse(localStorage.getItem("points_v6"));

    if (savedTasks) setTasks(savedTasks);
    else setTasks(initialTasks);

    if (savedActive) setActiveTaskId(savedActive);
    if (savedPoints) setUserPoints(savedPoints);
  }, []);

  useEffect(() => {
    localStorage.setItem("tasks_v6", JSON.stringify(tasks));
    localStorage.setItem("active_task_v6", JSON.stringify(activeTaskId));
    localStorage.setItem("points_v6", JSON.stringify(userPoints));
  }, [tasks, activeTaskId, userPoints]);

  const isCooldown = (task) =>
    task.lastCompleted && Date.now() - task.lastCompleted < SIX_HOURS;
  const getCooldownTime = (task) => {
    const remaining = SIX_HOURS - (Date.now() - task.lastCompleted);
    const h = Math.floor(remaining / 3600000);
    const m = Math.floor((remaining % 3600000) / 60000);
    return `${h}h ${m}m`;
  };
  const getFinishUnlockTime = (task) => {
    if (!task.startedAt) return null;
    const remaining = THIRTY_MINUTES - (Date.now() - task.startedAt);
    const m = Math.max(0, Math.floor(remaining / 60000));
    const s = Math.max(0, Math.floor((remaining % 60000) / 1000));
    return `${m}m ${s}s`;
  };

  const startTask = (taskId) => {
    if (activeTaskId) return;
    setActiveTaskId(taskId);
    setTasks((prev) =>
      prev.map((t) => (t.id === taskId ? { ...t, startedAt: Date.now() } : t))
    );
  };

  const finishTask = (taskId) => {
    const task = tasks.find((t) => t.id === taskId);
    if (!task) return;

    const id = Date.now();
    setFlyingPoints((prev) => [...prev, { id, points: task.points }]);

    setTimeout(() => {
      setUserPoints((p) => p + task.points);
      setFlyingPoints((prev) => prev.filter((f) => f.id !== id));
    }, 1200);

    setTasks((prev) =>
      prev.map((t) =>
        t.id === taskId
          ? { ...t, lastCompleted: Date.now(), startedAt: null, images: [] }
          : t
      )
    );
    setActiveTaskId(null);
  };

  const uploadImage = (taskId, file) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      setTasks((prev) =>
        prev.map((t) =>
          t.id === taskId ? { ...t, images: [...t.images, reader.result] } : t
        )
      );
    };
    reader.readAsDataURL(file);
  };

  return (
    <div className="min-h-screen  p-8 relative">
      {/* User Points */}
      <div className="w-full flex justify-center">
        <div className="text-center mb-10 text-xl font-bold text-green-700 w-fit relative bg-white/40 p-5 rounded-full">
          Your Points:{" "}
          <span className="text-green-500 text-2xl font-extrabold drop-shadow-[0_0_10px_#00ff91]">
            {userPoints}
          </span>
        </div>
      </div>
      {/* Flying points animation */}
      {flyingPoints.map((f) => (
        <div
          key={f.id}
          className="absolute left-1/2 top-[80px] text-green-500 font-bold text-xl animate-fly-point"
        >
          +{f.points}
        </div>
      ))}

      <h1 className="text-4xl md:text-5xl font-extrabold text-center mb-12 text-green-600 drop-shadow-[0_0_10px_#00ff91]">
        Daily CleanTM Tasks
      </h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-10 max-w-7xl mx-auto">
        {tasks.map((task) => {
          const cooldown = isCooldown(task);
          const isActive = activeTaskId === task.id;
          const someTaskActive = activeTaskId && !isActive;
          const canFinish =
            isActive && Date.now() - task.startedAt >= THIRTY_MINUTES;

          const difficultyColors = {
            Easy: "bg-green-500 shadow-[0_0_6px_#00ff91]",
            Medium: "bg-yellow-500 shadow-[0_0_6px_#ffee00]",
            Hard: "bg-red-500 shadow-[0_0_6px_#ff3300]",
          };

          return (
            <div
              key={task.id}
              className="
                relative rounded-3xl p-6 bg-white/10 backdrop-blur-md
                border border-green-400/40 shadow-[0_0_15px_5px_rgba(0,255,145,0.05)]
                hover:shadow-[0_0_25px_15px_rgba(0,255,145,0.2)]
                transition-all duration-500 flex flex-col justify-between
                overflow-hidden
              "
            >
              {/* Neon top line */}
              <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-green-400 via-green-300 to-green-400 animate-pulse-neon"></div>

              {/* Task Header */}
              <div className="flex justify-between items-center mb-3 relative z-10">
                <h2 className="text-2xl font-bold text-green-500 drop-shadow-[0_0_6px_#00ff91]">
                  {task.title}
                </h2>
                <div
                  className={`flex items-center gap-1 px-2 py-1 rounded-full text-white text-sm ${
                    difficultyColors[task.difficulty]
                  }`}
                >
                  <Star className="w-4 h-4" /> {task.difficulty}
                </div>
              </div>

              {/* Description */}
              <p className="text-gray-800 relative z-10">{task.description}</p>
              <p className="text-green-400 font-semibold relative z-10">
                Points: {task.points}
              </p>

              {/* Images */}
              {task.images.length > 0 && (
                <div className="flex flex-wrap mt-3 gap-2 relative z-10">
                  {task.images.map((img, i) => (
                    <img
                      key={i}
                      src={img}
                      className="h-20 w-20 rounded-xl border border-green-400 object-cover"
                    />
                  ))}
                </div>
              )}

              {/* Status */}
              <div className="mt-4 text-center text-sm text-gray-500 relative z-10">
                {cooldown
                  ? `Refreshes in ${getCooldownTime(task)}`
                  : isActive
                  ? `Finish unlocks in ${getFinishUnlockTime(task)}`
                  : someTaskActive
                  ? "Another task in progress..."
                  : ""}
              </div>

              {/* Buttons */}
              <div className="mt-4 flex flex-col gap-2 relative z-10">
                {!cooldown && !isActive && !someTaskActive && (
                  <button
                    onClick={() => startTask(task.id)}
                    className="w-full py-2.5 rounded-full bg-green-500 hover:bg-green-600 font-bold transition shadow-md hover:shadow-[0_0_20px_#00ff91]"
                  >
                    Start Task
                  </button>
                )}
                {isActive && (
                  <button
                    disabled={!canFinish}
                    onClick={() => finishTask(task.id)}
                    className={`w-full py-2.5 rounded-full font-bold transition shadow-md ${
                      canFinish
                        ? "bg-green-500 hover:bg-green-600 hover:shadow-[0_0_20px_#00ff91]"
                        : "bg-gray-400 cursor-not-allowed"
                    }`}
                  >
                    Finish Task
                  </button>
                )}
              </div>

              {/* File Upload */}
              {isActive && (
                <input
                  type="file"
                  onChange={(e) => uploadImage(task.id, e.target.files[0])}
                  className="mt-3 block w-full text-sm text-green-700 file:bg-green-500 file:text-white file:px-4 file:py-2 file:rounded-full cursor-pointer relative z-10"
                />
              )}
            </div>
          );
        })}
      </div>

      <style jsx>{`
        @keyframes pulse-neon {
          0%,
          100% {
            opacity: 0.6;
          }
          50% {
            opacity: 1;
          }
        }
        .animate-pulse-neon {
          animation: pulse-neon 2s infinite ease-in-out;
        }

        @keyframes flyPoint {
          0% {
            opacity: 1;
            transform: translateY(0) scale(1);
          }
          100% {
            opacity: 0;
            transform: translateY(-120px) scale(1.5);
          }
        }
        .animate-fly-point {
          animation: flyPoint 1.2s ease-in-out forwards;
        }
      `}</style>
    </div>
  );
}
